/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Mono';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/pt-mono-v7-latin_cyrillic-regular.eot'); /* IE9 Compat Modes */
  src: local('PT Mono'), local('PTMono-Regular'),
       url('../fonts/pt-mono-v7-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/pt-mono-v7-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/pt-mono-v7-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/pt-mono-v7-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/pt-mono-v7-latin_cyrillic-regular.svg#PTMono') format('svg'); /* Legacy iOS */
}

/* roboto-condensed-regular - latin_cyrillic */
